import React, { useState } from 'react';
import axios from 'axios';
import { navigate } from 'gatsby';
import { routes } from '../constants';

const ContactForm = () => {
  const [inputs, setInputs] = useState({
    name: '',
    email: '',
    subject: '',
    message: '',
  });

  const [status, setStatus] = useState({
    submitted: false,
    submitting: false,
    info: { error: false, msg: null },
  });

  const handleServerResponse = (ok, msg) => {
    if (ok) {
      setStatus({
        submitted: true,
        submitting: false,
        info: { error: false, msg: msg },
      });
      setInputs({
        name: '',
        email: '',
        subject: '',
        message: '',
      });
    } else {
      setStatus({
        info: { error: true, msg: msg },
      });
    }
  };

  const handleChange = e => {
    e.persist();

    setInputs(prev => ({
      ...prev,
      [e.target.id]: e.target.value,
    }));

    setStatus({
      submitted: false,
      submitting: false,
      info: { error: false, msg: null },
    });
  };

  const handleSubmit = e => {
    e.preventDefault();

    setStatus(prevStatus => ({ ...prevStatus, submitting: true }));

    const url = 'https://formspree.io/f/xjvplkky';
    const data = {
      ...inputs,
      _subject: inputs.subject,
    };

    axios
      .post(url, data)
      .then(response => {
        navigate(routes.THANK_YOU);
        // handleServerResponse(
        //   true,
        //   'Thank you, your message has been submitted.'
        // );
      })
      .catch(error => {
        handleServerResponse(false, error.response.data.error);
      });
  };

  return (
    <>
      <form onSubmit={handleSubmit}>
        <div className="my-6 flex flex-col sm:flex-row">
          <div className="w-full sm:w-1/2 sm:mr-4">
            <label htmlFor="name" className="block mb-1 leading-5 text-mdgray">
              Name
            </label>
            <input
              required
              id="name"
              type="text"
              name="name"
              value={inputs.name}
              onChange={handleChange}
              className="mb-6 sm:mb-0 appearance-none border rounded w-full py-2 px-2 text-mdgray leading-tight focus:border-ltgray focus:outline-none"
            />
          </div>
          <div className="w-full sm:w-1/2 sm:ml-4">
            <label htmlFor="email" className="block mb-1 leading-5 text-mdgray">
              Email
            </label>
            <input
              required
              id="email"
              type="email"
              name="email"
              value={inputs.email}
              onChange={handleChange}
              className="appearance-none border rounded w-full py-2 px-2 text-mdgray leading-tight focus:border-ltgray focus:outline-none"
            />
          </div>
        </div>
        <div className="mb-6">
          <label htmlFor="subject" className="block mb-1 leading-5 text-mdgray">
            Subject
          </label>
          <input
            required
            id="subject"
            type="text"
            name="subject"
            value={inputs.subject}
            onChange={handleChange}
            className="appearance-none border rounded w-full py-2 px-2 text-mdgray leading-tight focus:border-ltgray focus:outline-none"
          />
        </div>
        <div>
          <label htmlFor="message" className="block mb-1 leading-5 text-mdgray">
            Message
          </label>
          <textarea
            required
            id="message"
            type="text"
            name="message"
            value={inputs.message}
            onChange={handleChange}
            className="h-40 resize-none appearance-none border rounded w-full py-2 px-2 text-mdgray leading-tight focus:border-ltgray focus:outline-none"
          ></textarea>
        </div>
        <button
          type="submit"
          className={`mt-5 ${
            status.submitting ? 'bg-ltgray' : 'bg-primary'
          } text-white px-6 py-2 rounded`}
          disabled={status.submitting}
        >
          {!status.submitting
            ? !status.submitted
              ? 'Submit'
              : 'Submitted'
            : 'Submitting...'}
        </button>
      </form>
      {status.info.error && (
        <div className="mt-5 leading-5 text-mdgray">
          Error: {status.info.msg}
        </div>
      )}
      {!status.info.error && status.info.msg && (
        <p className="mt-5 leading-5 text-mdgray">{status.info.msg}</p>
      )}
    </>
  );
};

export default ContactForm;
